@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to{
    top: 50%;
    opacity: 1;
  }
}
@keyframes fade-in {
  from {
      opacity: 0;
      transform: translateY(2rem);
  }
  to{
    opacity: 1;
    transform: translateY(0rem);
  }
}

#toast-container{
  top: 3rem!important;
  animation: fade-in 250ms ease-in-out;
}